import Vue from 'vue'
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import {
	faPhone, faEnvelope, faFaucet,
	faHome, faCarSide, faIdCard,
	faAngleRight, faAngleLeft, faTimes
} from '@fortawesome/free-solid-svg-icons'
import { faFacebookSquare, faFacebookMessenger } from '@fortawesome/free-brands-svg-icons'
library.add(
	faPhone, faEnvelope, faFaucet, faFacebookSquare, faFacebookMessenger,
	faHome, faCarSide, faIdCard,
	faAngleRight, faAngleLeft, faTimes
)

Vue.component('fa-icon', FontAwesomeIcon)